import AddIcon from '@mui/icons-material/Add';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import DeleteIcon from '@mui/icons-material/Delete';
import FlagIcon from '@mui/icons-material/Flag';
import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, Grid, IconButton, Slider, TextField, Tooltip, Typography } from "@mui/material";
import MuiAlert, { AlertProps } from '@mui/material/Alert';
import { styled } from '@mui/material/styles';
import Loading from 'components/Loading';
import PriceSummary from 'components/PriceSummary';
import { FC, forwardRef, memo, useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Link, useParams } from 'react-router-dom';
import { useDocumentContext } from "state/documentContext";
import { useFlashMessage } from "state/FlashMessageContext";
import ClientPreventiveHeadComponent from "../../../components/ClientPreventiveHeadComponent/index";
import EuroTextField from '../../../components/EuroTextField/index';
import MarkdownEditor from '../../../components/MarkdownEditor/index';
import ProductPriceSummary from '../../../components/ProductPriceSummary';
import SummaryComponentList from "../../../components/SummaryQuote/SummaryComponentList";
import { useAppState } from "../../../state/stateContext";
import { ComponentType, DocumentDataDataType, ProductType, UserType } from "../../../types/index";
import { formatPrice } from "../../../utils/format-price";
import DocumentFooter from './DocumentFooter';

const Alert = forwardRef<HTMLDivElement, AlertProps>(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const PreventiveForm: FC<{ activeRevisionLabel: string | null, revisionData: DocumentDataDataType, user?: UserType }> = ({ activeRevisionLabel, revisionData }) => {
    const { t } = useTranslation();
    const { showMessage } = useFlashMessage();
    const { hash } = useParams<{ hash: string }>();
    const {
        originalDocumentData,
        updatedDocumentData,
        setUpdatedDocumentData,
        updateNestedDocumentField,
        getDocument,
        error,
        loading
    } = useDocumentContext();
    const { user, getProducts, products: allProducts } = useAppState();

    const [products, setProducts] = useState<ProductType[]>(updatedDocumentData?.data?.addedProducts || []);
    const [isSearchModalOpen, setIsSearchModalOpen] = useState(false);
    const [searchTerm, setSearchTerm] = useState("");
    const [filteredProducts, setFilteredProducts] = useState<ProductType[]>([]);
    const [paymentTerms, setPaymentTerms] = useState<string>(
        updatedDocumentData?.data?.paymentTerms || 
        "MODALITA' DI PAGAMENTO: Acconto 10% e saldo alla consegna o alla richiesta dei documenti di collaudo (in mancanza del versamento dell'acconto non sar possibile procedere con l'evasione dell'offerta)"
    );

    useEffect(() => {
        if (hash) {
            getDocument(hash);
        }
    }, [hash, getDocument]);

    useEffect(() => {
        if (updatedDocumentData?.data?.addedProducts) {
            setProducts(updatedDocumentData.data.addedProducts);
        }
    }, [updatedDocumentData]);

    useEffect(() => {
        if (revisionData) {
            setUpdatedDocumentData((prevData) => ({
                ...prevData,
                data: revisionData,
            }));
        }
    }, [revisionData, setUpdatedDocumentData]);

    useEffect(() => {
        getProducts();
    }, []);

    useEffect(() => {
        if (updatedDocumentData?.data?.paymentTerms) {
            setPaymentTerms(updatedDocumentData.data.paymentTerms);
        }
    }, [updatedDocumentData]);

    useEffect(() => {
        setFilteredProducts(
            allProducts.filter(product =>
                product.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                product.description.toLowerCase().includes(searchTerm.toLowerCase())
            )
        );
    }, [searchTerm, allProducts]);

    const handleInputChange = (index: number, field: string, value: string | number) => {
        const updatedProducts = [...products];
        const numericValue = typeof value === 'string' ? parseFloat(value) : value;
        updatedProducts[index] = {
            ...updatedProducts[index],
            [field]: isNaN(numericValue) ? value : numericValue
        };
        setProducts(updatedProducts);
        updateNestedDocumentField(['data', 'addedProducts'], updatedProducts);
    };

    const handleComponentsChange = (productIndex: number, updatedComponents: ComponentType[]) => {
        const updatedProducts = products.map((product, i) =>
            i === productIndex ? { ...product, components: updatedComponents } : product
        );
        setProducts(updatedProducts);
        updateNestedDocumentField(['data', 'addedProducts'], updatedProducts);
    };

    const calculateDiscountedPrice = (price: number, discount: number | null | undefined) => {
        const safeDiscount = discount ?? 0;
        return price * (1 - safeDiscount / 100);
    };

    const handleAddProduct = () => {
        setIsSearchModalOpen(true);
    };

    const handleCloseSearchModal = () => {
        setIsSearchModalOpen(false);
        setSearchTerm("");
    };

    const handleSelectProduct = (selectedProduct: ProductType) => {
        const updatedProducts = [...products, selectedProduct];
        setProducts(updatedProducts);
        updateNestedDocumentField(['data', 'addedProducts'], updatedProducts);
        showMessage('AddedItemToPreventive', 'success');
        handleCloseSearchModal();
    };

    const handleRemoveProduct = (productIndex: number) => {
        const updatedProducts = products.filter((_, index) => index !== productIndex);
        setProducts(updatedProducts);
        updateNestedDocumentField(['data', 'addedProducts'], updatedProducts);
        showMessage('RemovedItemFromPreventive', 'success');
    };

    const handleMoveUp = useCallback((index: number) => {
        if (index === 0) return;
        const updatedProducts = [...products];
        [updatedProducts[index - 1], updatedProducts[index]] = [updatedProducts[index], updatedProducts[index - 1]];
        setProducts(updatedProducts);
        updateNestedDocumentField(['data', 'addedProducts'], updatedProducts);
    }, [products, updateNestedDocumentField]);
    
    const handleMoveDown = useCallback((index: number) => {
        if (index === products.length - 1) return;
        const updatedProducts = [...products];
        [updatedProducts[index + 1], updatedProducts[index]] = [updatedProducts[index], updatedProducts[index + 1]];
        setProducts(updatedProducts);
        updateNestedDocumentField(['data', 'addedProducts'], updatedProducts);
    }, [products, updateNestedDocumentField]);

    const handleCloseSnackbar = (event?: React.SyntheticEvent, reason?: string) => {
        if (reason === 'clickaway') {
            return;
        } else {
            window.location.reload();
        }
    };

    const handlePaymentTermsChange = (value: string) => {
        setPaymentTerms(value);
        updateNestedDocumentField(['data', 'paymentTerms'], value);
    };

    if (loading) {
        return <Loading />;
    }

    if (error) {
        return <Box>{t(error)}</Box>;
    }

    if (!originalDocumentData) {
        return <Box>{t('No document data available')}</Box>;
    }

    return (
        <Box sx={{ maxWidth: '1200px', margin: '0 auto', background: '#fff', p: 3, paddingBottom: '300px' }}>
            {activeRevisionLabel && (
                <Box>
                    <Alert
                        onClose={handleCloseSnackbar}
                        severity="info"
                        sx={{ position: 'fixed', zIndex: 9999, backgroundColor: '#ffef4c', color: 'black' }}
                        iconMapping={{
                            warning: <FlagIcon fontSize="inherit" />
                        }}
                    >
                        {t('YouAreViewing', { activeRevisionLabel })}
                    </Alert>
                </Box>
            )}

            <ClientPreventiveHeadComponent />

            {/* LIST OF PRODUCTS */}
            <Box sx={{ mt: 4, mb: 6 }}>
                {products.length === 0 ? (
                    <Box sx={{
                        py: 4,
                        textAlign: 'center',
                        color: 'text.secondary',
                        border: '1px dashed',
                        borderColor: 'divider',
                        borderRadius: 1
                    }}>
                        {t('NoItemsYet')}
                    </Box>
                ) : (
                    products.map((product, productIndex) => (
                        <ProductBox key={product.id}>
                            <Tooltip title={t("RemoveProduct")} arrow>
                                <DeleteButton
                                    onClick={() => handleRemoveProduct(productIndex)}
                                    aria-label={t("RemoveProduct")}
                                >
                                    <DeleteIcon />
                                </DeleteButton>
                            </Tooltip>
                            <Grid container spacing={4}>
                                <Grid item xs={12} md={4}>
                                    <Box>
                                        {/* Product Header with Name and Reorder Arrows */}
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                                            <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                                <Link
                                                    to={`/product/${product.id}`}
                                                    style={{ textDecoration: 'none', color: 'inherit' }}
                                                    sx={{
                                                        '&:hover': {
                                                            textDecoration: 'underline',
                                                        }
                                                    }}
                                                >
                                                    <Typography variant="h6">{product.name}</Typography>
                                                </Link>
                                                {/* Reorder Arrow Buttons */}
                                                {user?.id && (
                                                    <Box sx={{ display: 'flex', flexDirection: 'column', ml: 1 }}>
                                                        <IconButton
                                                            size="small"
                                                            onClick={() => handleMoveUp(productIndex)}
                                                            disabled={productIndex === 0}
                                                            aria-label="Move Up"
                                                        >
                                                            <ArrowUpwardIcon fontSize="small" />
                                                        </IconButton>
                                                        <IconButton
                                                            size="small"
                                                            onClick={() => handleMoveDown(productIndex)}
                                                            disabled={productIndex === products.length - 1}
                                                            aria-label="Move Down"
                                                        >
                                                            <ArrowDownwardIcon fontSize="small" />
                                                        </IconButton>
                                                    </Box>
                                                )}
                                            </Box>
                                        </Box>

                                        {/* Product Image */}
                                        <Box
                                            component="img"
                                            src={product.imgUrl}
                                            alt={product.name}
                                            sx={{
                                                width: '100%',
                                                maxHeight: 300,
                                                objectFit: 'contain',
                                                mb: 2,
                                                borderRadius: 1
                                            }}
                                        />

                                        {/* Product Price */}
                                        <EuroTextField
                                            label={t("ProductPrice")}
                                            value={product.price}
                                            onChange={(value) => handleInputChange(productIndex, 'price', value)}
                                            margin="dense"
                                            fullWidth
                                            readOnly={!user?.id}
                                        />

                                        {/* Discount and Discounted Price (Visible to Authorized Users) */}
                                        {user?.id && (
                                            <>
                                                {/* Discount Slider */}
                                                <Box sx={{ display: 'flex', alignItems: 'center', mt: 2 }}>
                                                    <Typography variant="subtitle1" sx={{ mr: 2 }}>{t("Discount")}</Typography>
                                                    <Slider
                                                        value={product.discount || 0}
                                                        onChange={(_, newValue) => handleInputChange(productIndex, 'discount', newValue as number)}
                                                        aria-labelledby="product-discount-slider"
                                                        valueLabelDisplay="auto"
                                                        step={1}
                                                        marks
                                                        min={0}
                                                        max={100}
                                                        sx={{ flexGrow: 1 }}
                                                    />
                                                    <Typography variant="body2" sx={{ ml: 2 }}>
                                                        {product.discount || 0}%
                                                    </Typography>
                                                </Box>

                                                {/* Display Discounted Price */}
                                                <Box sx={{ display: 'flex', alignItems: 'center', mt: 2 }}>
                                                    <Typography variant="subtitle1">{t("Price")}:</Typography>
                                                    <Typography variant="body1" sx={{ ml: 1 }}>
                                                        {formatPrice(calculateDiscountedPrice(product.price, product.discount))}
                                                    </Typography>
                                                    {product.discount > 0 && (
                                                        <>
                                                            <DiscountedPrice variant="body2">
                                                                {formatPrice(product.price)}
                                                            </DiscountedPrice>
                                                            <DiscountPercentage variant="body2">
                                                                (-{product.discount}%)
                                                            </DiscountPercentage>
                                                        </>
                                                    )}
                                                </Box>
                                            </>
                                        )}

                                        {/* Product Description */}
                                        <Box mt={2}>
                                            <Typography variant="subtitle1">{t("Description")}</Typography>
                                            <MarkdownEditor
                                                value={product.description || ""}
                                                onChange={(value) => handleInputChange(productIndex, 'description', value)}
                                                readOnly={!user?.id}
                                            />
                                        </Box>

                                        {/* Product Price Summary */}
                                        <ProductPriceSummary
                                            product={product}
                                            components={product.components || []}
                                        />
                                    </Box>
                                </Grid>

                                {/* Product Components */}
                                <Grid item xs={12} md={8}>
                                    <Box>
                                        <SummaryComponentList
                                            components={product.components || []}
                                            onComponentChange={(updatedComponents) => handleComponentsChange(productIndex, updatedComponents)}
                                        />
                                    </Box>
                                </Grid>
                            </Grid>
                        </ProductBox>
                    ))
                )}
            </Box>


            {user?.id && (
                <>
                    {/* ADD PRODUCT BUTTON */}
                    <Box sx={{ mb: 6 }}>
                        <Button
                            startIcon={<AddIcon />}
                            variant="contained"
                            onClick={handleAddProduct}
                        >
                            {t("AddProduct")}
                        </Button>
                    </Box>

                    {/* ADD PRODUCT SEARCH DIALOG */}
                    <Dialog open={isSearchModalOpen} onClose={handleCloseSearchModal} fullWidth maxWidth="md">
                        <DialogTitle>{t("AddProduct")}</DialogTitle>
                        <DialogContent>
                            <TextField
                                autoFocus
                                margin="dense"
                                label={t("SearchProducts")}
                                type="text"
                                fullWidth
                                variant="outlined"
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                            />
                            <Box sx={{ mt: 2 }}>
                                {filteredProducts.map((product) => (
                                    <Box
                                        key={product.id}
                                        sx={{
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'space-between',
                                            p: 2,
                                            borderBottom: '1px solid #e0e0e0',
                                            cursor: 'pointer'
                                        }}
                                        onClick={() => handleSelectProduct(product)}
                                    >
                                        <Box>
                                            <Typography variant="subtitle1">{product.name}</Typography>
                                            <Typography variant="body2" color="text.secondary">
                                                {product.description.substring(0, 100)}...
                                            </Typography>
                                        </Box>
                                        <Typography variant="subtitle1">{formatPrice(product.price)}</Typography>
                                    </Box>
                                ))}
                            </Box>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleCloseSearchModal}>{t("Cancel")}</Button>
                        </DialogActions>
                    </Dialog>
                </>
            )}

            {/* PAYMENT TERMS SECTION */}
            <Box sx={{ 
                mb: 6, 
                mt: 4, 
                p: 4, 
                border: '1px solid #e0e0e0', 
                borderRadius: 2,
                boxShadow: '0 2px 8px rgba(0,0,0,0.05)',
                backgroundColor: '#f9f9f9',
                position: 'relative',
                overflow: 'hidden'
            }}>
                <Box 
                    sx={{ 
                        position: 'absolute', 
                        top: 0, 
                        left: 0, 
                        width: '8px', 
                        height: '100%', 
                        backgroundColor: 'primary.main' 
                    }} 
                />
                <Typography 
                    variant="h6" 
                    sx={{ 
                        mb: 3, 
                        fontWeight: 'bold',
                        display: 'flex',
                        alignItems: 'center',
                        color: 'primary.main'
                    }}
                >
                    <Box 
                        component="span" 
                        sx={{ 
                            mr: 1,
                            display: 'inline-flex',
                            p: 0.5,
                            borderRadius: '50%',
                            backgroundColor: 'primary.light',
                            color: 'white'
                        }}
                    >
                        <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1.41 16.09V20h-2.67v-1.93c-1.71-.36-3.16-1.46-3.27-3.4h1.96c.1 1.05.82 1.87 2.65 1.87 1.96 0 2.4-.98 2.4-1.59 0-.83-.44-1.61-2.67-2.14-2.48-.6-4.18-1.62-4.18-3.67 0-1.72 1.39-2.84 3.11-3.21V4h2.67v1.95c1.86.45 2.79 1.86 2.85 3.39H14.3c-.05-1.11-.64-1.87-2.22-1.87-1.5 0-2.4.68-2.4 1.64 0 .84.65 1.39 2.67 1.91s4.18 1.39 4.18 3.91c-.01 1.83-1.38 2.83-3.12 3.16z"/>
                        </svg>
                    </Box>
                    {t("PaymentTerms")}
                </Typography>
                
                <Box sx={{ 
                    backgroundColor: 'white', 
                    p: 2, 
                    borderRadius: 1,
                    border: '1px solid #eaeaea'
                }}>
                    {user?.id ? (
                        <MarkdownEditor
                            value={paymentTerms}
                            onChange={handlePaymentTermsChange}
                            readOnly={!user?.id}
                        />
                    ) : (
                        <Typography 
                            variant="body1" 
                            sx={{ 
                                lineHeight: 1.6,
                                fontWeight: paymentTerms.includes('MODALITA') ? 'medium' : 'normal'
                            }}
                        >
                            {paymentTerms}
                        </Typography>
                    )}
                </Box>
                
                {user?.id && (
                    <Typography 
                        variant="caption" 
                        sx={{ 
                            display: 'block', 
                            mt: 1, 
                            color: 'text.secondary',
                            fontStyle: 'italic'
                        }}
                    >
                        {t("EditPaymentTermsInfo", "Edit the payment terms above to customize the conditions")}
                    </Typography>
                )}
            </Box>

            {/* PRICE SUMMARY */}
            <Box sx={{ mb: 6 }}>
                <PriceSummary />
            </Box>

            {/* SIGNATURE FOOTER */}
            <Box sx={{ mb: 6 }}>
                <DocumentFooter />
            </Box>
        </Box>
    );
};

export default PreventiveForm;

const DiscountedPrice = styled(Typography)({
    textDecoration: 'line-through',
    marginLeft: '4px',
    color: '#999',
});

const DiscountPercentage = styled(Typography)({
    marginLeft: '4px',
    color: '#4caf50',
});

const ProductBox = memo(styled(Box)({
    marginBottom: '32px',
    padding: '24px',
    backgroundColor: '#ffffff',
    borderRadius: '4px',
    position: 'relative',
    transition: 'all 0.3s ease',
    '&:hover': {
        boxShadow: '0 0 0 2px #e4e4e4',
    },
}));

const DeleteButton = styled(IconButton)({
    position: 'absolute',
    top: '8px',
    right: '8px',
    color: '#000000',
    '&:hover': {
        color: '#ff0000',
    },
});