import React from 'react';
import { Box, Typography } from '@mui/material';
import { useTranslation } from 'react-i18next';
import { ProductType, ComponentType } from '../../types';
import { formatPrice } from '../../utils/format-price';

interface ProductPriceSummaryProps {
    product: ProductType;
    components: ComponentType[];
}

const ProductPriceSummary: React.FC<ProductPriceSummaryProps> = ({ product, components }) => {
    const { t } = useTranslation();

    const calculateDiscountedPrice = (price: number, discount: number | null | undefined) => {
        const safeDiscount = discount ?? 0;
        const result = price * (1 - safeDiscount / 100);
        return result;
    };

    const componentTotal = components.reduce((total, component) => {
        if (component.included) {
            const discountedPrice = calculateDiscountedPrice(component.price, component.discount);
            // Defensive: default quantity to 1 if missing or invalid
            const quantity = (typeof component.quantity === 'number' && !isNaN(component.quantity)) ? component.quantity : 1;
            return total + discountedPrice * quantity;
        }
        return total;
    }, 0);

    const productDiscountedPrice = calculateDiscountedPrice(product.price, product.discount);
    const totalPrice = productDiscountedPrice + componentTotal;

    return (
        <Box sx={{ mt: 3, p: 2, border: '1px solid', borderColor: 'divider', borderRadius: 1 }}>
            <Typography variant="h6" gutterBottom>{t("ProductPriceSummary")}</Typography>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                <Typography>{t("ProductPrice")}</Typography>
                <Typography>{formatPrice(productDiscountedPrice)} {t('EUR')}</Typography>
            </Box>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                <Typography>{t("ComponentsTotal")}</Typography>
                <Typography>{formatPrice(componentTotal)} {t('EUR')}</Typography>
            </Box>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', fontWeight: 'bold' }}>
                <Typography>{t("TotalPrice")} <span style={{fontSize: '.8rem', color: 'grey'}}>({t('WithoutTaxes')})</span></Typography>
                <Typography>{formatPrice(totalPrice)} {t('EUR')}</Typography>
            </Box>
        </Box>
    );
};

export default ProductPriceSummary;